import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import './styles/lot.css';
import Cookies from 'universal-cookie';

function Cart() {
    var lot = [];
    const cookies = new Cookies();

    if (cookies) {
        var cookie = cookies.get('Cart')
        for (let id = 0; id < cookie.length; id+=2){
            axios({
                method: "GET",
                url: `http://127.0.0.1:8000/api/lot/${cookie[id]}/`
            }).then(response => {
                lot.push(response.data)
            })
        }
    }
    
    return(
        <div className="lot">
            {Object.values(lot).map(l => (
                <div className='lot'>
                    <div className="desc">
                        <Link className='lotTitle Title'>{l.name}</Link>
                        <Link className="shopTitle Title">Продавец: {l.shop}</Link>
                        <div className="lotDesc">
                            Материал: {l.material}. Назначение: {l.purpose}.
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )};
    export default Cart;